<template>
  <div class="page" style="padding-top: 50px;">
    <AnimateHeader
    bg-color="#f5f5f5"
    menu-bg-color="#f0f0f0"
    color="#3A3A3A"
    title="关于钱咖"
    :on-back-button-click="back"
    :expanded="false"
    :animate="false"
    >
    </AnimateHeader>
    <div class="page-content qk-about">
      <div class="icon"><img src="//qianka.b0.upaiyun.com/images/380841c00621e4028e8f549fc8bf5dce.png" alt=""></div>
      <div @click="showPopup()">
        <div class="title">微信公众号</div>
        <div class="content">qiankakefu<img src="//qianka.b0.upaiyun.com/images/6b7c32c778d51145910d1cad14ed8c2e.png" alt=""></div>
      </div>

      <div>
        <div class="title">官方微博</div>
        <div class="content">钱咖</div>
      </div>

      <div>
        <div class="title">商务合作</div>
        <div class="content last">biz@qianka.com</div>
      </div>

      <div class="bottom">
        <p @click="showAgreement()">《<span>用户服务协议</span>》</p>
      </div>
    </div>
  </div>
</template>
<style lang='scss' scoped>
  @import "../../../scss/mixins";

  .qk-about {
    padding-top: 110px;
    background: #FFFFFF;
    text-align: center;

    .icon {
      height: 60px;
      margin-bottom: 30px;

      img {
        width: 60px;
        height: 60px;
      }
    }

    .title {
      height: 17px;
      line-height: 17px;
      font-size: 12px;
      color: #b5b5b5;
      margin-bottom: 4px;

      @include font-regular();
    }

    .content {
      height: 28px;
      line-height: 28px;
      font-size: 20px;
      color: #3a3a3a;
      margin-bottom: 26px;

      @include font-regular();

      img {
        width: 15px;
        height: 15px;
        vertical-align: middle;
        margin-left: 6px;
      }

      &.last {
        margin-bottom: 118px;
      }

    }

    .bottom {
      height: 22px;
      font-size: 12px;
      line-height: 22px;
      color: #b5b5b5;

      span {
        text-decoration: underline;
      }

      @include font-regular();
    }
  }
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import Agreement from '../../Agreement.vue'
  import {popup_show_qrcode} from '../popup.js'

  export default {
    components: {
      AnimateHeader
    },

    mounted () {
      $modal.fromComponent(Agreement, {
        // title: '用户服务协议',
        // theme: 'dark'
      }).then(modal => {
        this.agreement = modal
      })

      $bus.$on('closeAgreementModal', () => {
        this.agreement.hide()
      })
    },

    destroyed () {
      if (this.agreement) $modal.destroy(this.agreement)
    },

    methods: {
      back () {
        $router.back('/user')
      },

      showPopup () {
        let popup = $popup.fromTemplate(
          popup_show_qrcode.template(),
          popup_show_qrcode.options
        )
        popup.show()
      },

      showAgreement () {
        this.agreement.show()
      }
    }
  }
</script>




